@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limeGreen: hsl(163, 72%, 41%);
  --brightRed: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagramStart: hsl(37, 97%, 70%);
  --instagramEnd: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text-primary: hsl(228, 34%, 66%);
  --dark-text-secondary: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-top-bg: hsl(227, 47%, 96%);
  --light-card: hsl(227, 47%, 96%);
  --light-text-primary: hsl(228, 12%, 44%);
  --light-text-secondary: hsl(230, 17%, 14%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  font-size: rem(14);
  color: var(--light-text-primary);
  background: var(--light-bg);
  transition: 0.3s;
}
body h3 {
  margin-top: 3rem;
  margin-bottom: 1rem;
}
body h1,
body h2,
body h4 {
  color: var(--light-text-secondary);
}
body .card,
body .overview {
  background: var(--light-card);
}
body.dark {
  background: var(--dark-bg);
  color: var(--dark-text-primary);
  transition: 0.3s;
}
body.dark h1,
body.dark h2,
body.dark h4 {
  color: var(--dark-text-secondary);
}
body.dark .card,
body.dark .overview {
  background: var(--dark-card);
}

.container {
  max-width: 1024px;
  margin: 0 auto;
}

.attribution {
  font-size: 11px;
  text-align: center;
  margin: 2rem 0;
}

.attribution a {
  color: #3e52a3;
}

@media screen and (max-width: 768px) {
  .container {
    margin: 0 0.6rem;
  }
}
h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.facebook {
  border-top: 0.3125rem solid var(--facebook);
}

.twitter {
  border-top: 0.3125rem solid var(--twitter);
}

.youtube {
  border-top: 0.3125rem solid var(--youtube);
}

.instagram {
  border-top: 5px solid;
  border-image-slice: 1;
  border-image-source: linear-gradient(to right, var(--instagramStart), var(--instagramEnd));
}

.green {
  color: var(--limeGreen);
}

.red {
  color: var(--brightRed);
}

.main__card {
  display: flex;
  justify-content: center;
  gap: 1.25rem;
  flex-wrap: wrap;
  margin-top: -6.25rem;
}
.main__card .card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 0.3125rem;
  row-gap: 0.625rem;
  width: 14.375rem;
  padding: 1.25rem;
}
.main__card .card .profile {
  display: flex;
  align-items: center;
  column-gap: 0.3125rem;
}
.main__card .card .profile img {
  width: 1.25rem;
  height: 1.25rem;
}
.main__card .card .profile .user {
  font-size: 0.75rem;
  font-weight: 600;
}
.main__card .card .followers {
  text-align: center;
}
.main__card .card .followers h2 {
  font-size: 3rem;
  font-weight: 700;
  margin: 0;
}
.main__card .card .followers p {
  letter-spacing: 0.125rem;
  margin: 0;
}
.main__card .card .stats {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 0.3125rem;
  margin-top: 1rem;
  letter-spacing: 0.2px;
  font-weight: 700;
  font-size: 0.75rem;
}

@keyframes slidein {
  from {
    left: 2px;
  }
  to {
    left: 28px;
  }
}
@keyframes slideout {
  from {
    left: 28px;
  }
  to {
    left: 2px;
  }
}
.toggle {
  display: flex;
  align-items: center;
  column-gap: 0.625rem;
}
.toggle .btn {
  border: none;
  width: 3.125rem;
  height: 1.5625rem;
  border-radius: 30px;
  background-color: var(--toggle);
  position: relative;
  cursor: pointer;
}
.toggle .btn .circle {
  content: "";
  position: absolute;
  left: 2px;
  top: 2px;
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 50%;
  background-color: var(--toggle-bg-start);
  animation-duration: 0.2s;
  animation-name: slideout;
}
.toggle .btn .circle.is__dark {
  animation-duration: 0.2s;
  animation-name: slidein;
  left: auto;
  right: 2px;
  background-color: var(--toggle-bg-end);
}

body header {
  background: var(--light-top-bg);
}

body.dark header {
  background: var(--dark-top-bg);
}

header {
  height: 15.625rem;
}
header .header__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 0;
}
header .header__body .title h1 {
  margin-bottom: 0.25rem;
}
header .header__body .title p {
  font-weight: 600;
  margin: 0;
}

.green {
  color: var(--limeGreen);
}

.red {
  color: var(--brightRed);
}

.main__overview {
  display: flex;
  justify-content: center;
  gap: 1.25rem;
  flex-wrap: wrap;
}
.main__overview .overview {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 0.625rem;
  row-gap: 0.625rem;
  width: 14.375rem;
  height: 6.25rem;
  padding: 0.9375rem;
}
.main__overview .overview .overview__header {
  display: flex;
  justify-content: space-between;
  display: flex;
  align-items: center;
  column-gap: 0.3125rem;
}
.main__overview .overview .overview__header p {
  font-size: 0.875rem;
  font-weight: 600;
}
.main__overview .overview .overview__header img {
  width: 1.25rem;
  height: 1.25rem;
}
.main__overview .overview .overview__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main__overview .overview .overview__body h4 {
  font-size: 2rem;
}
.main__overview .overview .overview__body .stats {
  display: flex;
  align-self: flex-end;
  justify-content: center;
  align-items: center;
  column-gap: 0.3125rem;
  letter-spacing: 0.2px;
  font-weight: 700;
  font-size: 0.75rem;
}